import React from 'react';
// import classNames from 'classnames';

import './App.scss';

const goodsFromServer: string[] = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

type State = {
  selectedGoods: string[];
};

class App extends React.Component<{}, State> {
  state = {
    selectedGoods: [],
  };

  add = (good: string) => {
    this.setState((state) => {
      state.selectedGoods.push(good);

      return {
        selectedGoods: state.selectedGoods,
      };
    });
  };

  render() {
    const { selectedGoods } = this.state;

    return (
      <div className="App">
        <h1 className="App__title">
          {
            selectedGoods
              ? `Selected good: - ${selectedGoods}`
              : 'No goods selected'
          }
        </h1>
        <button
          type="button"
          className="App__button"
        >
          X
        </button>
        <ul className="list">
          {goodsFromServer.map(good => (
            <li
              key={good}
              className="list__good"
            >
              {good}
              <button
                type="button"
                className="list__button"
                onClick={() => {
                  this.add(good);
                }}
              >
                +
              </button>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

export default App;
